import React, { useState } from "react";
import { makeStyles, useTheme } from "@material-ui/styles";
import {
  Avatar,
  Container,
  CssBaseline,
  Grid,
  Paper,
  Button,
  Typography,
  Table,
  TableRow,
  TableCell,
  TextField,
  TableBody,
  TableHead
} from "@material-ui/core";
import LockOutlinedIcon from '@material-ui/icons/LockOutlined'
import { useInput, useSubmit } from "hooks/form";
import { useClock } from "hooks/timer";
import { usePos } from "hooks/mouse";
import { useScroll, useSize } from "hooks/window";
import AuthService from "services/auth.service"

//create a hook for classes objects
const useStyles = makeStyles(
  theme => ({
    root: {
      minWidth: "500px",
      height: "100vh"
    },
    paper: {
      marginTop: theme.spacing(2),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      marginTop: theme.spacing(3),
      width: '100%', // Fix IE 11 issue.
    },
    input: {
      marginBottom: theme.spacing(3)
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    }, 

  })
);

const validations = {
  // eslint-disable-next-line
  EMAIL: /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
  MOBILE: /^\d{10}$/,
  CID: /^\d{13}$/,
  NUMBER: /^\d*$/,
  DATE:/^\d{1,2}\/\d{1,2}\/\d{4}$/,
  STRING:/^[A-Za-zก-์\-0-9]+$/,
  PASSWORD:/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,20}$/
};

const Signup=()=>{
  // use of hooks to bring classes style sheet in (usually done with HOC)
  const classes = useStyles();
  // we can alse use a hook to access the theme
  const theme = useTheme();

  function handleValidation(value, regex) {
    // we could get fancy here with validations based on type of input
    // could be put in a form hook library and imported
    if (value && regex && value.match(regex)) return true;
    return false;
  }

  const email = useInput("email", "", handleValidation, validations.EMAIL);
  const mobile = useInput("mobile", "", handleValidation, validations.MOBILE);
  const firstname = useInput("firstname", "", handleValidation, validations.STRING);
  const lastname = useInput("lastname", "", handleValidation, validations.STRING);
  const cid = useInput("cid", "", handleValidation, validations.CID);
  const password = useInput("password", "", handleValidation, validations.PASSWORD);

  // the data we're going to submit, just using a hook to display
  const [data, setData] = useState(null);

  function handleSuccess(data) {
    // we're just setting the state here, but typically this would
    // be sent to the server for further validation and persistence
    setData(data);

    AuthService.register(data).then(
      (response) => {
console.log('OK', data)
      },
      (error) => {
        const resMessage =
console.log('notOK')
      }
    );
  }

  //pass in array of hooks to validate onSubmit (that's where the data is)
  const submit = useSubmit([firstname, lastname, email, mobile, cid, password], handleSuccess);

  // custom hook using both useState and useEffect
  const time = useClock();

  // custom hook using both useState and useEffect
  const pos = usePos();

  // custom hook using both useState and useEffect
  const scroll = useScroll();

  // custom hook using both useState and useEffect
  const size = useSize();

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>        
      <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component='h1'variant="h5">Sign up</Typography>
        <form className={classes.form} {...submit.props}>

          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
            <TextField label="First Name"  fullWidth variant="outlined" {...firstname.props} />
            {firstname.props.error && (
              <Typography variant="body1" color="error">
                Invalid First Name
              </Typography>
            )}
            </Grid>
            <Grid item xs={12} sm={6}>
            <TextField label="Last Name" fullWidth variant="outlined" {...lastname.props} />
            {lastname.props.error && (
              <Typography variant="body1" color="error">
                Invalid Last Name
              </Typography>
            )}
            </Grid>
            <Grid item xs={12}>
            <TextField label="CID" fullWidth variant="outlined" {...cid.props} />
            {cid.props.error && (
              <Typography variant="body1" color="error">
                Invalid CID, need 13 digit
              </Typography>
            )}
            </Grid>
            
            <Grid item xs={12}>
            <TextField label="Email" fullWidth variant="outlined" {...email.props} />
            {email.props.error && (
              <Typography variant="body1" color="error">
                Invalid Email address
              </Typography>
            )}
            </Grid>
            <Grid item xs={12}>
            <TextField label="Mobile" fullWidth variant="outlined" {...mobile.props} />
            {mobile.props.error && (
              <Typography variant="body1" color="error">
                Invalid Mobile Number, need 10 digit
              </Typography>
            )}
            </Grid>
            
            <Grid item xs={12}>
            <TextField label="Password" fullWidth variant="outlined" {...password.props} />
            {password.props.error && (
              <Typography variant="body1" color="error">
                Password needs 6 to 20 characters contain at least one numeric digit, one uppercase, and one lowercase
              </Typography>
            )}
          </Grid>
          </Grid>
          <Button type="submit" color="primary" fullWidth
          className={classes.submit} variant="contained">
            Submit
          </Button>
          {submit.errorItems && submit.errorItems.length > 0 && (
            <Typography variant="body1" color="error">
              {`Please fix ${submit.errorItems && submit.errorItems.length} form
            field error(s)`}
            </Typography>
          )}

        </form>
        {data && (
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Input Field</TableCell>
                <TableCell>Validated Input</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {data.map((item, index) => (
                <TableRow key={`form-${index}`}>
                  <TableCell>{item.name}</TableCell>
                  <TableCell>{item.value}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        )}
      </div>
    </Container>
  );
}

export default Signup;
